.notes_display {
  border-right: 1px solid rgb(0, 0, 0, 0.5);
  /* background: lightblue; */
  width: 350px;
  flex-shrink: 0;
  background: #f1f1f1;
}

.notes_display h2 {
  padding: 30px 90px;
  text-align: center;
  font-size: 30px;
}
.notes_display form {
  width: 100%;
}
.notes_display form input {
  display: block;
  background: #f5f5f5;
  width: 100%;
  height: 100%;
  border: 1px solid rgb(0, 0, 0, 0.5);
  border-right: none;
  padding: 25px 12px;
  font-size: 16px;
}
.notes_display form input::placeholder {
  font-size: 16px;
  font-family: Roboto, sans-serif;
}

.notes_list {
  max-height: calc(100vh - 180px);
  list-style-type: none;
  overflow-y: auto;
}

.notes_list li:hover {
  background: #ebebeb;
}

@media screen and (max-width: 768px) {
  .notes_display h2 {
    padding: 30px 0px;
    text-align: center;
    font-size: 30px;
  }
  .notes_display {
    width: 210px;
  }
}
